<template>
    <b-nav-item-dropdown id="login-dropdown" right>
        <!-- Using button-content slot -->
        <template slot="button-content">
        <b>Entrar ou cadastrar</b>
        </template>
        <!-- <b-dropdown-item> -->
        <div id="login-dropdown-content">
            <div class="row">
                <div class="col-md-12">
                    Entrar via
                    <div class="form-group">
                        <label class="sr-only" for="exampleInputEmail">Endereço de email</label>
                        <input type="email" class="form-control" id="exampleInputEmail" placeholder="Endereço de email" v-model="user.email" required>
                    </div>
                    <div class="social-buttons">
                        <b-button class="btn btn-fb" @click="socialLogin()">
                            <i class="fa fa-facebook"></i> Facebook
                        </b-button>
                    </div>
                    ou
                    <form class="form" role="form" accept-charset="UTF-8" id="login-nav">
                        <div class="form-group">
                            <label class="sr-only" for="exampleInputEmail2">Endereço de email</label>
                            <input type="email" class="form-control" id="exampleInputEmail2" placeholder="Endereço de email" v-model="user.email" required>
                        </div>
                        <div class="form-group">
                            <label class="sr-only" for="exampleInputPassword2">Senha</label>
                            <input type="password" class="form-control" id="exampleInputPassword2" placeholder="Senha" v-model="user.password" required>
                            <div class="help-block text-right">
                                <a href="">Esqueceu sua senha ?</a>
                            </div>
                        </div>
                        <div class="form-group">
                            <button type="button" class="btn btn-primary btn-block" @click="login()">Entrar</button>
                        </div>
                        <div class="checkbox">
                            <label>
                                <input type="checkbox"> Mantenha-me conectado
                            </label>
                        </div>
                    </form>
                </div>
                <div class="bottom text-center">
                    Novo aqui ?
                    <b-link to="registration"><b>Junte-se a nós</b></b-link>
                </div>
            </div>
        </div>
        <!-- </b-dropdown-item> -->
    </b-nav-item-dropdown>
</template>

<script>
export default {
  data () {
    return {
      user: {
        email: '',
        password: ''
      }
    }
  },
  methods: {
    login () {
      this.$store.dispatch('login', {
        email: this.user.email,
        password: this.user.password
      }).then(() => {
        this.$router.push('/restaurants')
      })
    },
    socialLogin () {
      this.$store.dispatch('socialLogin', {
        email: this.user.email
      }).then(() => {
        this.$router.push('/restaurants')
      })
    }
  }
}
</script>

<style scoped>
.dropdown-menu {
    background-color: rgba(255,255,255,.8) !important;
}

.navbar-light .navbar-nav .nav-link {
    color: black;
}

#login-dropdown-content {
    min-width: 250px;
    padding: 14px 14px 0;
    overflow: hidden;
}

#login-dropdown-content .help-block {
    font-size: 12px
}

#login-dropdown-content .bottom {
    border-top: 1px solid #ddd;
    clear: both;
    padding: 14px;
    width: 100%;
}

#login-dropdown-content .social-buttons {
    margin: 12px 0
}

#login-dropdown-content .social-buttons button {
    width: 100%;
}

#login-dropdown-content .form-group {
    margin-bottom: 10px;
}

.btn-fb {
    color: #fff;
    background-color: #3b5998;
}

.btn-fb:hover {
    color: #fff;
    background-color: #496ebc
}

@media(max-width:768px) {
    #login-dropdown-content {
        background-color: inherit;
        color: #fff;
    }
    #login-dropdown-content .bottom {
        background-color: inherit;
        border-top: 0 none;
    }
}
</style>



